import React, { Component } from 'react';
import { Row, Col, Tag } from 'antd';

const getColorPlanById = (id, planSigns) => {
  const sign = planSigns.filter(s => s.id === id)[0];
  return !sign ? '#fff' : sign.color;
};

const getTextColorPlanById = (id, planSigns) => {
  const backgroudColor = getColorPlanById(id, planSigns);
  if (['#FFFFFF', '#fff', '#FFFF00'].includes(backgroudColor)) {
    return '#000';
  }
  return '#fff';
};

export default class NotePlan extends Component {
  state = {};
  render() {
    return (
      <Row style={{ marginTop: 20 }}>
        {(this.props.planSigns || []).map(plan => (
          <Col span={8}>
            <Tag style={{ border: '1px solid #EEEEEE' }} color={plan.color}>
              <span style={{ color: getTextColorPlanById(plan.id, this.props.planSigns) }}>
                {plan.id}
              </span>
            </Tag>
            <span>{plan.label}</span>
          </Col>
        ))}
      </Row>
    );
  }
}



