import React, { useState, useEffect } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnUpload } from 'components/common/button';
import { Card, Modal } from 'antd';
import moment from 'moment';
import useModal from 'components/common/ModalHook/useModal.js';
import { CartHeadAction } from 'components/common';
import FormSearch from './FormSearch';
import DataSheet from './DataSheet';
import FormCreateNew from './FormCreateNew';
import NotePlan from './NotePlan';
import YearEduPlanInfo from './YearEduPlanInfo';
import {
  createUpdateEducationPlan,
  deleteEducationPlan,
  getEducationPlans,
} from '../../../services/educationPlan';

import { getPlanSigns } from '../../../services/planSign';
import UploadFileModal from './UploadFileModal';

const getWeekRange = (startDate, endDate) => {
  const result = [];
  let currentNoWeek = 0;
  let startDateCheck = startDate.clone();
  let i = 0;
  if (startDateCheck.day() === 0) {
    // if = Sunday
    startDateCheck = startDateCheck.add(1, 'day');
  }
  while (startDateCheck.isBefore(endDate) && i < 100) {
    const dayOfWeek = startDateCheck.day();
    const endWeekCurrentCheck = startDateCheck.clone().add(7 - dayOfWeek, 'day');
    result.push({
      noWeek: currentNoWeek + 1,
      month: startDateCheck.clone().format('MM/YYYY'),
      fromDate: startDateCheck.clone().format('DD/MM/YYYY'),
      toDate: endWeekCurrentCheck.clone().format('DD/MM/YYYY'),
    });
    currentNoWeek += 1;
    startDateCheck = endWeekCurrentCheck.clone().add(1, 'day');
    i += 1;
  }
  return result;
};

const WholeSchoolTrainingPlanPage = () => {
  const [visible, setVisible] = useState(false);
  const [yearEduPlanSelected, setYearEduPlanSelected] = useState(null);
  const [yearEduPlanIdSelected, setYearEduPlanIdSelected] = useState(null);
  const [classIds, setClassIds] = useState([]);
  const [weekEdus, setWeekEdusEdus] = useState([]);
  const [yearEduPlanFormValue, setYearEduPlanFormValue] = useState(null);
  const [educationPlanObj, setEducationPlanObj] = useState({});
  const [planSigns, setPlanSigns] = useState([]);
  const [, showModalUploadFileModal] = useModal('WholeSchoolTrainingPlanPage_UploadFileModal');

  const callGetEducationPlans = async () => {
    const res = await getEducationPlans();
    setEducationPlanObj(
      res.data.items.reduce((obj, item) => {
        obj[item.id] = item;
        return obj;
      }, {})
    );
  };

  const callGetPlanSigns = async () => {
    const res = await getPlanSigns();
    setPlanSigns(res.data.items);
  };
  useEffect(() => {
    callGetEducationPlans();
    callGetPlanSigns();
  }, []);

  useEffect(() => {
    setYearEduPlanSelected(educationPlanObj[yearEduPlanIdSelected]);
  }, [educationPlanObj, yearEduPlanIdSelected]);

  useEffect(() => {
    if (!yearEduPlanSelected) {
      return;
    }
    const fromDate = moment(yearEduPlanSelected.fromDate);
    const toDate = moment(yearEduPlanSelected.toDate);
    setWeekEdusEdus(getWeekRange(fromDate.clone(), toDate.clone()));
  }, [yearEduPlanSelected]);

  const handleSubmitFormSearch = async values => {
    setYearEduPlanIdSelected(values.yearEduPlanId);
    setClassIds(values.classIds);
  };

  const handleCancelModalCreateNew = () => {
    setVisible(false);
    setYearEduPlanFormValue(null);
  };

  const handleSubmitFormEduPlan = async values => {
    const data = { ...values, fromDate: values.dateRange[0], toDate: values.dateRange[1] };
    await createUpdateEducationPlan(data);
    await callGetEducationPlans();
    handleCancelModalCreateNew();
  };

  const handleClickBtnEditEduYearEduPlan = () => {
    setVisible(true);
    setYearEduPlanFormValue(yearEduPlanSelected);
  };

  const handleDeleteYearEduPlan = async yearEduPlan => {
    await deleteEducationPlan({ id: yearEduPlan.id });
    await callGetEducationPlans();
    setYearEduPlanSelected(null);
  };

  return (
    <Card title="Kế hoạch đào tạo toàn trường">
      <CartHeadAction>
        <BtnSave title="Cập nhật" onClick={() => setVisible(true)} />
        <BtnUpload onClick={() => showModalUploadFileModal()} />
      </CartHeadAction>
      <FormSearch educationPlanObj={educationPlanObj} handleSubmit={handleSubmitFormSearch} />
      <YearEduPlanInfo
        onClickBtnEdit={handleClickBtnEditEduYearEduPlan}
        handleDeleteYearEduPlan={handleDeleteYearEduPlan}
        yearEduPlan={yearEduPlanSelected}
        classIds={classIds}
      />
      {classIds && classIds.length > 0 && (
        <DataSheet
          planSigns={planSigns}
          yearEduPlan={yearEduPlanSelected}
          classIds={classIds}
          weekEdus={weekEdus}
        />
      )}
      <NotePlan planSigns={planSigns} />
      <Modal
        footer={null}
        onCancel={handleCancelModalCreateNew}
        title="Kế hoạch"
        visible={visible}
      >
        <FormCreateNew
          visible={visible}
          yearEduPlan={yearEduPlanFormValue}
          onCancel={handleCancelModalCreateNew}
          handleSubmit={handleSubmitFormEduPlan}
        />
      </Modal>
      <UploadFileModal educationPlanObj={educationPlanObj} />
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <WholeSchoolTrainingPlanPage />
  </Page>
);



