import React, { Component } from 'react';
import { Form, DatePicker, Button } from 'antd';
import { getYearEducations } from 'services/yearEducationService';
import { SelectAjax } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import moment from 'moment';

const { RangePicker } = DatePicker;
@Form.create({ name: 'FormCreateNew' })
export default class FormCreateNew extends Component {
  componentDidMount = () => {
    this.initData(this.props.yearEduPlan);
  };

  componentWillReceiveProps = nextProps => {
    if (nextProps.visible !== this.props.visible) {
      this.initData(nextProps.yearEduPlan);
    }
    // if (!nextProps.visible && nextProps.yearEduPlan.id) {
    //   this.props.form.resetFields();
    // }
  };

  initData = data => {
    if (data) {
      this.props.form.setFieldsValue({
        ...data,
        dateRange: [moment(data.fromDate), moment(data.toDate)],
      });
    } else {
      this.props.form.resetFields();
    }
  };

  handleSubmit = async e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      const { yearEduPlan } = this.props;
      this.props.handleSubmit({ ...values, id: yearEduPlan ? yearEduPlan.id : undefined });
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="login-form">
        {' '}
        <Form.Item label="Năm học" hasFeedback>
          {getFieldDecorator('yearEduId', {
            rules: [requiredRule],
          })(
            <SelectAjax
              service={getYearEducations}
              setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
            />
          )}
        </Form.Item>
        <Form.Item label="Ngày ">
          {getFieldDecorator('dateRange', {
            initialValue: '',
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<RangePicker style={{ width: '100%' }} />)}
        </Form.Item>
        <div className="" style={{ textAlign: 'right' }}>
          <Button onClick={this.props.onCancel}>Hủy</Button>
          <Button onClick={this.handleSubmit} type="primary">
            Lưu
          </Button>
        </div>
      </Form>
    );
  }
}



