import { request } from 'utils';

const prefix = '/api/education';

export const getPlanSigns = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: '/PlanSign',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



