import React, { useState, useEffect } from 'react';
import ReactDataSheet from 'react-datasheet';
import 'react-datasheet/lib/react-datasheet.css';
import { Col, Row, Button, Modal, message, Spin } from 'antd';
import FormSelectPlan from './FormSelectPlan';
import { getClassById } from '../../../services/class';
import {
  createUpdatePlanDetail,
  getPlanDetailByEducationPlan,
  deletePlanDetail,
} from '../../../services/planDetail';
import './styles.scss';

const groupBy = (xs, key) => {
  return xs.reduce((rv, x) => {
    (rv[x[key]] = rv[x[key]] || []).push(x);
    return rv;
  }, {});
};

const getCellSelected = () => {
  return Array.from(document.getElementsByClassName('selected')).map(item => {
    return item.dataset.code;
  });
};

const validateHaveCellSelected = () => {
  const cellSelected = getCellSelected();
  if (cellSelected.length === 0) {
    message.warning('Vui lòng chọn thời gian');
    return false;
  }
  return true;
};

const createCellCodeStr = ({ classId, noWeek, id }) => {
  return JSON.stringify({
    id,
    noWeek,
    classId,
  });
};

const getValuePlanDetailByCode = (planDetails = [], classId, noWeek) => {
  const planDetailByCode = planDetails.filter(
    pd => pd.classId === classId && pd.week === noWeek
  )[0];
  if (!planDetailByCode) {
    return { value: '' };
  }
  return { id: planDetailByCode.id, value: planDetailByCode.planSignId };
};

const createDataGrid = ({ classes, weekEdus, planDetails }) => {
  // for data rows
  const gridNew = classes.map(c => {
    // for data code
    return weekEdus.map(week => {
      const { id, value } = getValuePlanDetailByCode(planDetails, c.id, week.noWeek);
      return {
        code: createCellCodeStr({ id, classId: c.id, noWeek: week.noWeek }),
        className: c.name,
        value,
      };
    });
  });
  return gridNew;
};

export default ({ weekEdus, yearEduPlan = {}, classIds = [], planSigns = [] }) => {
  const [visibleSelectPlan, setVisibleSelectPlan] = useState(false);
  const [grid, setGrid] = useState([]);
  const [codeSelected, setCodeSelected] = useState([]);
  const [classes, setClasses] = useState([]);
  const [planDetails, setPlanDetails] = useState([]);
  const [loadingGetPlan, setLoadingGetPlan] = useState(false);
  const [loadingGetClassInfo, setLoaddingGetClassInfo] = useState(false);

  const callGetClassInfo = async () => {
    setLoaddingGetClassInfo(true);
    const classData = [];
    for (let i = 0; i < classIds.length; i += 1) {
      // eslint-disable-next-line no-await-in-loop
      const res = await getClassById(classIds[i]);
      classData.push(res.data);
    }
    setClasses(classData);
    setLoaddingGetClassInfo(false);
  };

  const callGetPlanDetailByEducationPlan = async () => {
    setLoadingGetPlan(true);
    const res = await getPlanDetailByEducationPlan({
      educationPlanId: yearEduPlan.id,
      classIds,
      isPagingEnabled: false,
    });
    setPlanDetails(res.data.items);
    setLoadingGetPlan(false);
  };

  useEffect(() => {
    callGetClassInfo();
  }, [classIds]);

  useEffect(() => {
    if (classes.length === 0) {
      return;
    }
    callGetPlanDetailByEducationPlan();
    setGrid(createDataGrid({ weekEdus, classes }));
  }, [weekEdus, classes]);

  useEffect(() => {
    if (classes.length === 0) {
      return;
    }
    setGrid(createDataGrid({ weekEdus, classes, planDetails }));
  }, [weekEdus, classes, planDetails]);

  const showModalSelectPlan = () => {
    if (!validateHaveCellSelected()) return;
    setVisibleSelectPlan(true);
    const cellSelected = getCellSelected();
    setCodeSelected(cellSelected);
  };

  const handleCancelSelectPlan = () => {
    setCodeSelected([]);
    setVisibleSelectPlan(false);
  };

  const handleSubmitSelectPlan = async planSignId => {
    const data = codeSelected.map(codeStr => {
      const code = JSON.parse(codeStr);
      return {
        id: code.id,
        classId: code.classId,
        week: code.noWeek,
        planId: yearEduPlan.id,
        planSignId,
      };
    });
    await createUpdatePlanDetail(data);
    await callGetPlanDetailByEducationPlan();
    setVisibleSelectPlan(false);
  };

  const handleDeletePlan = async () => {
    if (!validateHaveCellSelected()) return;
    const cellSelected = getCellSelected();
    const ids = cellSelected.map(code => JSON.parse(code).id).filter(id => id);
    await deletePlanDetail({ ids });
    await callGetPlanDetailByEducationPlan();
  };

  const getColorPlanById = id => {
    const sign = planSigns.filter(s => s.id === id)[0];
    return !sign ? '#fff' : sign.color;
  };

  const getTextColorPlanById = id => {
    const backgroudColor = getColorPlanById(id);
    if (['#FFFFFF', '#fff', '#FFFF00'].includes(backgroudColor)) {
      return '#000';
    }
    return '#fff';
  };

  if (!yearEduPlan || !yearEduPlan.id || classes.length === 0) {
    return null;
  }
  const monthEdus = groupBy(weekEdus, 'month');
  return (
    <Row>
      <Col span={24}>
        <Spin spinning={loadingGetPlan || loadingGetClassInfo}>
          <ReactDataSheet
            style={{ width: '100%' }}
            data={grid}
            sheetRenderer={sheetProps => (
              <table
                style={{ width: '100%' }}
                className={`${sheetProps.className} table-sheet-training-year`}
              >
                <thead>
                  <tr>
                    <th
                      style={{ background: '#fff', border: '0', textAlign: 'left', height: 70 }}
                      colSpan="1000"
                    >
                      <div style={{ position: 'absolute' }}>
                        <Button onClick={handleDeletePlan}>Xóa sự kiện</Button>
                        <Button onClick={showModalSelectPlan}>Cập nhật sự kiện</Button>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <th rowSpan="3">Tên lớp</th>
                    <th rowSpan="3">Số SV</th>
                    {Object.entries(monthEdus).map(([month, weeks]) => {
                      return <th colSpan={weeks.length}>{month}</th>;
                    })}
                  </tr>
                  <tr>
                    {weekEdus.map(week => {
                      return <th>{week.noWeek}</th>;
                    })}
                  </tr>
                  <tr>
                    {weekEdus.map(week => {
                      return (
                        <th style={{ minWidth: 60 }}>
                          {week.fromDate.substring(0, 2)} - {week.toDate.substring(0, 2)}
                        </th>
                      );
                    })}
                  </tr>
                </thead>
                <tbody>{sheetProps.children}</tbody>
              </table>
            )}
            rowRenderer={rowProps => (
              <tr>
                <td>{classes[rowProps.row].label}</td>
                <td>{classes[rowProps.row].noOfStudent}</td>
                {rowProps.children}
              </tr>
            )}
            valueRenderer={cell => (
              <div
                style={{
                  background: getColorPlanById(cell.value),
                  height: 27,
                  textAlign: 'center',
                  verticalAlign: 'middle',
                  lineHeight: '27px',
                  color: getTextColorPlanById(cell.value),
                  border: cell.value !== '' ? '1px solid rgb(238, 238, 238)' : 'none',
                }}
              >
                {cell.value}
              </div>
            )}
            attributesRenderer={cell => ({ 'data-code': cell.code } || {})}
          />
        </Spin>
      </Col>
      <Modal
        title="Lựa chọn kế hoạch"
        visible={visibleSelectPlan}
        footer={null}
        onCancel={handleCancelSelectPlan}
        cancelText="Hủy"
        okText="Lưu"
      >
        <FormSelectPlan
          planSigns={planSigns}
          handleSelectPlan={handleSubmitSelectPlan}
          onCancel={handleCancelSelectPlan}
        />
      </Modal>
    </Row>
  );
};



