import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import SelectMultiAjax from 'components/common/input/SelectMultiAjax';
import { BtnSearch, BtnClear } from 'components/common/button';
import moment from 'moment';
import { getClasss } from 'services/class';
import { requiredRule } from '../../../utils/validateRules';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};

  componentWillReceiveProps = nextProps => {
    const yearEduPlanId = nextProps.form.getFieldValue('yearEduPlanId');
    if (yearEduPlanId && !nextProps.educationPlanObj[yearEduPlanId]) {
      nextProps.form.setFieldsValue({ yearEduPlanId: null });
    }
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) return;
      this.props.handleSubmit(values);
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const educationPlans = Object.values(this.props.educationPlanObj);
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate classes form={this.props.form} />
          <Col xs={24} sm={12} md={4}>
            <Form.Item label="Lớp">
              {getFieldDecorator('classIds', { rules: [requiredRule] })(
                <SelectMultiAjax
                  service={getClasss}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId }}
                  allowGetData={!!studyFieldId}
                  // open={!!studyFieldId && !classId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduPlanId', { rules: [requiredRule] })(
                <Select>
                  {educationPlans.map(eduPlan => (
                    <Select.Option value={eduPlan.id}>
                      {`${moment(eduPlan.fromDate).year()}-${moment(eduPlan.toDate).year()}`}
                    </Select.Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={2}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



