import React from 'react';
import moment from 'moment';
import { Alert, Popconfirm } from 'antd';
import { BtnEdit, BtnDelete, BtnExport } from 'components/common/button';
import { exportPlanDetailByEducationPlan } from '../../../services/planDetail';

export default ({ yearEduPlan, onClickBtnEdit, handleDeleteYearEduPlan, classIds }) => {
  if (!yearEduPlan) {
    return null;
  }
  const callExportPlanDetailByEducationPlan = async () => {
    await exportPlanDetailByEducationPlan({ educationPlanId: yearEduPlan.id, classIds });
  };
  return (
    <div style={{ marginTop: 20 }}>
      <Alert
        style={{
          padding: '15px',
        }}
        type="info"
        message={
          <div>
            <span style={{ fontSize: '14px' }}>
              Kế hoạch đào tạo năm học <strong>{moment(yearEduPlan.fromDate).year()}</strong> -
              <strong>{moment(yearEduPlan.toDate).year()}</strong> (
              {moment(yearEduPlan.fromDate).format('DD/MM/YYYY')} -{' '}
              {moment(yearEduPlan.toDate).format('DD/MM/YYYY')})
              <div
                style={{
                  float: 'right',
                  marginTop: '-6px',
                }}
              >
                <BtnEdit
                  onClick={onClickBtnEdit}
                  style={{
                    height: '35px',
                    marginRight: '10px',
                    maxWidth: '30px',
                    lineHeight: '28px',
                  }}
                />

                <Popconfirm
                  title="Bạn chắc chắn muốn xóa?"
                  onConfirm={() => handleDeleteYearEduPlan(yearEduPlan)}
                  okText="Xóa"
                  cancelText="Hủy"
                >
                  <BtnDelete
                    style={{
                      height: '35px',
                      marginRight: '10px',
                      maxWidth: '30px',
                      lineHeight: '28px',
                    }}
                  />
                </Popconfirm>
                <BtnExport onClick={callExportPlanDetailByEducationPlan} />
              </div>
            </span>
          </div>
        }
      />
    </div>
  );
};



