import React, { useState } from 'react';
import moment from 'moment';
import { message, Spin, Form, Upload, Button, Icon, Select, Col, Row } from 'antd';
import ModalHook from 'components/common/ModalHook';
import to from 'await-to-js';
import { requiredRule } from '../../../utils/validateRules';
import { uploadFilePlanDetail } from '../../../services/planDetail';
import useModal from '../../../components/common/ModalHook/useModal';

export const MODAL_ID = 'WholeSchoolTrainingPlanPage_UploadFileModal';

const UploadFileModal = ({ form, educationPlanObj }) => {
  const [loading, setLoading] = useState(false);
  const [, , hiddenModalUploadFileModal] = useModal('WholeSchoolTrainingPlanPage_UploadFileModal');
  const { getFieldDecorator, validateFields } = form;
  const handleOk = async () => {
    validateFields(async (error, values) => {
      if (error) {
        console.error('Form error');
      }
      const formData = new FormData();
      formData.append('file', values.file[0].originFileObj);
      formData.append('yearEduId', values.yearEduId);
      const [err] = await to(uploadFilePlanDetail(formData));
      if (err) {
        setLoading(false);
        return;
      }
      message.success('Upload thành công');
      hiddenModalUploadFileModal();
      setLoading(false);
    });
    return false;
  };
  const normFile = e => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList.slice(-1);
  };
  return (
    <ModalHook
      okButtonProps={{ disabled: loading }}
      cancelButtonProps={{ disabled: loading }}
      onOk={handleOk}
      width="500px"
      title="Tải file"
      id={MODAL_ID}
    >
      <Spin spinning={loading}>
        <Form className="form-inline-label" layout="vertical">
          <Row>
            <Col span="12">
              <Form.Item label="Năm học">
                {getFieldDecorator('yearEduId', { rules: [requiredRule] })(
                  <Select>
                    {Object.values(educationPlanObj).map(eduPlan => (
                      <Select.Option value={eduPlan.id}>
                        {`${moment(eduPlan.fromDate).year()}-${moment(eduPlan.toDate).year()}`}
                      </Select.Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span="12">
              <Form.Item label="">
                <div style={{ textAlign: 'left' }}>
                  {getFieldDecorator('file', {
                    rules: [requiredRule],
                    valuePropName: 'fileList',
                    getValueFromEvent: normFile,
                  })(
                    <Upload accept=".xlsx" beforeUpload={() => false} listType="text">
                      <Button>
                        <Icon type="upload" /> Chọn file
                      </Button>
                    </Upload>
                  )}
                </div>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Spin>
    </ModalHook>
  );
};

export default Form.create()(UploadFileModal);



