import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const createUpdatePlanDetail = (planDetails = []) =>
  request({
    prefix,
    url: `/PlanDetail/CreateUpdatePlanDetail`,
    method: 'POST',
    params: {},
    data: { planDetails },
  });

export const getPlanDetailByEducationPlan = ({
  educationPlanId,
  classIds,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/PlanDetail/GetPlanDetailByEducationPlan`,
    method: 'POST',
    params: {},
    data: { educationPlanId, classIds, page, pageSize, orderBy, isPagingEnabled },
  });

export const deletePlanDetail = ({ ids } = {}) =>
  request({
    prefix,
    url: `/PlanDetail/DeletePlanDetails`,
    method: 'DELETE',
    params: {},
    data: { ids },
  });

export const exportPlanDetailByEducationPlan = ({ educationPlanId, classIds } = {}) =>
  requestDownload({
    prefix,
    url: `/PlanDetail/ExportPlanDetailByEducationPlan`,
    method: 'POST',
    params: {},
    data: { educationPlanId, classIds },
  });

export const uploadFilePlanDetail = formData =>
  request({
    prefix,
    url: `/PlanDetail/UploadFile`,
    method: 'POST',
    params: {},
    data: formData,
  });



