import React, { Component } from 'react';
import { Form, Select, Button } from 'antd';

const { Option } = Select;
@Form.create({ name: 'FormSelectPlan' })
export default class FormSelectPlan extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { handleSelectPlan } = this.props;
        handleSelectPlan(values.planCode);
      }
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} style={{ marginBottom: 0 }} className="login-form">
        {' '}
        <Form.Item label="Kế hoạch" hasFeedback>
          {getFieldDecorator('planCode', {
            initialValue: '',
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(
            <Select>
              {(this.props.planSigns || []).map(plan => (
                <Option value={plan.id}>{plan.label}</Option>
              ))}
            </Select>
          )}
        </Form.Item>
        <div className="" style={{ textAlign: 'right' }}>
          <Button onClick={this.props.onCancel}>Hủy</Button>
          <Button onClick={this.handleSubmit} type="primary">
            Lưu
          </Button>
        </div>
      </Form>
    );
  }
}



