import { request } from 'utils';

const prefix = '/api/education';

export const getEducationPlans = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: `/EducationPlan`,
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createEducationPlan = ({ yearEduId, fromDate, toDate } = {}) =>
  request({
    prefix,
    url: '/EducationPlan',
    method: 'POST',
    params: {},
    data: { yearEduId, fromDate, toDate },
  });

export const updateEducationPlan = ({ id, yearEduId, fromDate, toDate } = {}) =>
  request({
    prefix,
    url: `/EducationPlan/${id}`,
    method: 'PUT',
    params: {},
    data: { id, yearEduId, fromDate, toDate },
  });

export const deleteEducationPlan = ({ id } = {}) =>
  request({
    prefix,
    url: `/EducationPlan/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });

export const createUpdateEducationPlan = ({ id, yearEduId, fromDate, toDate } = {}) =>
  request({
    prefix,
    url: `/EducationPlan/CreateUpdateEducationPlan`,
    method: 'POST',
    params: {},
    data: { id, yearEduId, fromDate, toDate },
  });



